<template>
  <div>
    <p>Decode Url String</p>
    <p>
      <router-link to="/">back to the contents</router-link>
    </p>
    <br>
    <div>
      <textarea v-model="before_string" id="before_string" rows="13" cols="30" placeholder="input area"></textarea>
      <textarea v-model="after_string" id="after_string" rows="13" cols="30" placeholder="result area" readonly></textarea>
    </div>
    <div>
      <!-- button -->
      <input value="encode string" type="button" @click="encode_string()">
      <input value="decode string" type="button" @click="decode_string()">
      <input value="clean data" type="button" @click="clean()">
    </div>
  </div>
</template>

<script>

export default {
  name: 'DecodeUrlView',
  data () {
    return {
      before_string: '',
      after_string: ''
    }
  },
  methods: {
    encode_string () {
      this.after_string = encodeURIComponent(this.before_string)
    },
    decode_string () {
      this.after_string = decodeURIComponent(this.before_string)
    },
    clean () {
      this.before_string = ''
      this.after_string = ''
    }
  }
}
</script>

